import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

function Square(props) {
	let style;

	if (props.status === 'winner') {
		style = {
			backgroundColor: 'red',
			color: 'white',
		};
	} else if (props.status === 'clicked') {
		style = {
			color: 'red',
		};
	}
	
	return (
		<button className="square" onClick={props.onClick} style={style}>
			{props.value}
		</button>
	);
}

class Board extends React.Component {
	renderSquare(i) {
		let status;

		if (this.props.winner && this.props.winner.line.includes(i)) {
			status = 'winner';
		} else if (this.props.clicked === i) {
			status = 'clicked';
		}

		return (
			<Square 
				value={this.props.squares[i]} 
				onClick={() => this.props.onClick(i)}
				status={status}
				key={'square-' + i}
			/>
		);
	}

	render() {
		let board = [];

		for (let i = 0; i < 3; i++) {
			let boardRows =[];

			for (let j = 0; j < 3; j++) {
				boardRows.push(this.renderSquare(3 * i + j));
			}

			board.push(<div className="board-row" key={'board-' + i}>{boardRows}</div>);
		}

		return (
			<div>
				{board}
			</div>
		);
	}
}

const initialGameState = {
	history: [{
		squares: Array(9).fill(null),
	}],
	location: [{
		col: null,
		row: null,
	}],
	stepNumber: 0,
	xIsNext: true,
	clicked: null,
	reversed: false,
};

class Game extends React.Component {
	constructor(props) {
		super(props);
		this.state = initialGameState;
	}

	handleClick(i) {
		const history = this.state.history.slice(0, this.state.stepNumber + 1);
		const location = this.state.location.slice(0, this.state.stepNumber + 1);
		const current = history[history.length - 1];
		const squares = current.squares.slice();

		if (calculateWinner(squares) || squares[i]) {
			return;
		}

		squares[i] = this.state.xIsNext ? 'X' : 'O';
		
		this.setState({
			history: history.concat([{
				squares: squares,
			}]),
			location: location.concat([{
				col: (i % 3),
				row: (Math.floor(i / 3)),
			}]),
			stepNumber: history.length,
			xIsNext: !this.state.xIsNext,
		})
	}

	jumpTo(step) {
		const location = this.state.location[step];

		this.setState({
			stepNumber: step,
			xIsNext: (step % 2) === 0,
			clicked: location.col + (3 * location.row),
		});
	}

	reverseOrder() {
		this.setState({
			reversed: !this.state.reversed,
		});
	}

	resetGame() {
		this.setState(initialGameState);
	}
	
	render() {
		const history = this.state.history;
		const location = this.state.location;
		const reversed = this.state.reversed;
		const clicked = this.state.clicked;
		const current = history[this.state.stepNumber];
		const winner = calculateWinner(current.squares);

		const moves = history.map((step, move) => {
			if (reversed) {
				move = history.length - 1 - move;
			}

			const coords = '(' + location[move].col + ',' + location[move].row + ')';
			const desc = move ? 'Go to move #' + move + ' ' + coords : 'Go to game start';

			return (
				<li key={move}>
					<button onClick={() => this.jumpTo(move)}>
						{desc}
					</button>
				</li>
			);
		});

		let status;

		if (!winner && this.state.stepNumber >= 9) {
			status = 'Draw'
		} else if (winner) {
			status = 'Winner: ' + winner.player;
		} else {
			status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
		}

		let order;

		if (reversed) {
			order = 'Sort ascending';
		} else {
			order = 'Sort descending';
		}

		return (
			<div className="game">
				<div className="game-board">
					<Board 
						squares={current.squares}
						onClick={(i) => this.handleClick(i)}
						winner={winner}
						clicked={clicked}
					/>
				</div>
				<div className="game-info">
					<div className="status">
						{status}
					</div>
					<div className="buttons">
						<button className="button-sort" onClick={() => this.reverseOrder()}>
							{order}
						</button>
						<button className="button-sort" onClick={() => this.resetGame()}>
							Reset game
						</button>
					</div>
					<ol>
						{moves}
					</ol>
				</div>
			</div>
		);
	}
}

ReactDOM.render(
	<Game />,
	document.getElementById('root')
);

function calculateWinner(squares) {
	const lines = [
		[0, 1, 2],
		[3, 4, 5],
		[6, 7, 8],
		[0, 3, 6],
		[1, 4, 7],
		[2, 5, 8],
		[0, 4, 8],
		[2, 4, 6],
	];
	for (let i = 0; i < lines.length; i++) {
		const [a, b, c] = lines[i];
		if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
			return {
				player: squares[a],
				line: [a, b, c],
			}
		}
	}
	return null;
}